$primaryColor: #026ccf;

html,
body,
#root {
    height: 100%;
    width: 100%;
}

.container {
    height: 100%;
    display: flex;
    flex-direction: column;

    .header {
        margin-bottom: 50px;
        min-height: 370px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        box-shadow: 0 10px 14px #e6e6e6;
        position: relative;
        z-index: 2;
        background-image: url(https://wac-cdn.atlassian.com/dam/jcr:e62e6b1b-9430-4eda-a733-0a053b2f5864/map-road-right.png?cdnVersion=1563),
            url(https://wac-cdn.atlassian.com/dam/jcr:1437a9b6-36ee-4c2a-9a46-82580072e551/map-road-left.png?cdnVersion=1563);
        background-position: right center, left center;
        background-repeat: no-repeat, no-repeat;
        background-size: contain, contain;
        @media only screen and (max-width: 860px){
            background-size: 30vw, 30vw;
            background-position: right top, left bottom;
        }
        h1 {
            font-size: 2.75rem;
            line-height: 1.18181818;
            color: #253858;
            letter-spacing: 0;
            margin: 0 50px;
        }
        p {
            color: #42526e;
            font-size: 1.25rem;
            letter-spacing: 0.3px;
            line-height: 1.4;
            margin: 20px 50px 40px 50px;
        }

        input[type="file"] {
            display: none;
        }

        .custom-file-upload {
            border: 1px solid #ccc;
            padding: 8px 24px;
            cursor: pointer;
            border-radius: 6px;
            background-color: #1d6f42;
            color: white;
            user-select: none;
            &:hover {
                background-color: rgb(27, 184, 97);
            }
        }

        .logo {
            background-image: url("logo.svg");
            width: 200px;
            background-repeat: no-repeat;
        }
    }

    .contentPanel {
        display: flex;
        flex-direction: column;
        align-items: center;

        .statusFilter {
            display: flex;
            @media only screen and (max-width: 860px){
                display: inline-grid;
            }
            h4 {
                padding-left: 20px;
                text-transform: uppercase;
                font-size: 1rem;
                font-weight: bold;
                letter-spacing: 0.1rem;
                padding-top: 13px;
                line-height: 1.2;
                margin-bottom: 24px;
                color: #253858;
                margin-top: 0;
            }

            button {
                display: block;
                padding: 8px 16px;
                color: #5e6c84;
                background: #f4f5f7;
                border-radius: 50px;
                cursor: pointer;
                font-size: 20px;
                border: none;
                margin: 10px;
                outline: none;

                &.selected {
                    background: #deebff;
                }
            }
        }

        .catagory {
            width: 800px;
            @media only screen and (max-width: 860px){
                width: 90%;
            }
            h2 {
                margin-top: 50px;
            }

            .item {
                box-shadow: 1px 1px 6px 0 #bbb;
                padding: 16px;
                margin-bottom: 10px;
                border-radius: 5px;

                & ~ .emptyStateText {
                    display: none;
                }

                h4 {
                    margin-bottom: 5px;
                    font-size: 1rem;
                    padding-bottom: 5px;
                    line-height: 1.2;
                    color: #253858;
                    margin-top: 0;
                }
                span {
                    float: right;
                    margin-bottom: 0;
                    padding: 0 7px;
                    border-radius: 5px;
                    font-size: 14px;
                    font-weight: bold;
                    font-family: "SF Pro Text", Arial, sans-serif;
                    background-color: #dfe1e5;
                    color: #6b778c;
                    line-height: 24px;
                    margin-left: 8px;

                    &.shipped {
                        background-color: #abf5d1;
                        color: #00875a;
                        display: none;
                    }

                    &.comingSoon {
                        background-color: #deebff;
                        color: #0052cc;
                        display: none;
                    }

                    &.inTheWorks {
                        background-color: #eae6ff;
                        color: #403294;
                        display: none;
                    }

                    &.isShipped ~ span.shipped,
                    &.isComingSoon ~ span.comingSoon,
                    &.isInTheWorks ~ span.inTheWorks {
                        display: block;
                    }
                }

                p {
                    font-size: 1rem;
                    line-height: 24px;
                    color: inherit;
                    margin: 10px 0;
                }
            }
        }
    }

    .emptyState {
        background-image: url("./roadmap.jpg");
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0.5;
        flex: 1 0 auto;
    }

    .footer {
        margin-top: 50px;
        flex-shrink: 0;
        background-color: #f5f5f5;
        padding: 30px;
        text-align: center;
        border-top: 1px solid #e6e6e6;

        span {
            font-size: 12px;
        }
    }
}
